import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Extra from './Extra'
import CasoDePrueba from './CasoDePrueba';
import NotFound from './NotFound';
import UNoRegistrado from './UNoRegistrado';
import Alumno from './Alumno';
import Profesor from './Profesor';

class Home extends Component {


  constructor() {
    super();
    this.state = {
      rol: '',
      check: false,
      user: {}
    };
  }

  componentWillMount() {
      
      axios.get('/User/all/role/'+this.props.cosas.email)
      .then(res => {
        this.setState({ rol: res.data, check: true });
        console.log(this.state.rol);
      });
      
     /*
      axios.get('/User/all/'+this.props.cosas.email)
      .then(res => {
        this.setState({ user: res.data });
        console.log(this.state.user);
      });
*/
  
  }





  render() {
    

    if (this.state.check){

      if (this.state.rol=='-1'){
        //return( <UNoRegistrado />);
        this.props.history.push("/UNoRegistrado")
        return null
      }
      else if (this.state.rol=='1'){
          this.props.history.push("/Alumno")
          return null
        }
      else if (this.state.user.role==2){
          this.props.history.push("/Profesor")
          return null
        }
        else if(this.state.rol==''){
          this.props.history.push("/"+this.state.user.role+"/")
          return null
        }
        else{
          this.props.history.push("/NotFound")
          return null
        }

    
    }
    
  
    
  }
}

export default Home


